import '../App.css';


const Formulario = (props) => {
    return ( 
        <div className="Form">
          
        <form onSubmit={props.enviar}>
        <div>
          <h3>Ingrese datos</h3>
          <label htmlFor="clave">Clave</label>
          <input
            type="text"
            placeholder="1232"
            onChange={props.guardarCambios}
            value={props.persona.clave}
            name="clave"
            disabled={props.desactivado}
          />
        </div>
        <div>
          <label htmlFor="nombre">Nombre</label>
          <input
            type="text"
            placeholder="Luna Sanchez"
            onChange={props.guardarCambios}
            value={props.persona.nombre}
            name="nombre"
          />
        </div>
        <div>
          <label htmlFor="email">Correo</label>
          <input
            type="email"
            placeholder="example@mail.com"
            onChange={props.guardarCambios}
            value={props.persona.correo}
            name="correo"
          />
        </div>
        <div>
          <label htmlFor="municipio">Municipio</label>
          <select name="municipio" value={props.persona.municipio} onChange={props.guardarCambios}>
            <option value="selecciona">selecciona</option>
            <option value="Tijuana">Tijuana</option>
            <option value="Rosarito">Rosarito</option>
            <option value="Ensenada">Ensenada</option>
          </select>
        </div>
        <button>Enviar</button>
        </form>
      </div>
    );
}
 
export default Formulario;