import '../App.css';
import { Button, Table } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Listado = (props) => {
    return ( 
        <div className="List">
        {
                    
            props.lista.length===0   
            ? <p>No hay registros</p>
            : 
        
             <Table striped bordered hover>
            <thead>
              <tr>
                <th>Clave</th>
                <th>Nombre</th>
                <th>Correo</th>
                <th>Municipio</th>
                <th></th>
                <th></th> 
              </tr>
            </thead>
              <tbody>
            {
              props.lista.map((a,index)=>
                <tr key={index}>
                    <td>{a.clave}</td>
                    <td>{a.nombre}</td>
                    <td>{a.correo}</td>
                    <td>{a.municipio}</td>
                    <td><Button onClick={()=>props.eliminar(a.clave)} variant="danger">Eliminar</Button></td>
                    <td><Button onClick={()=>props.modificar(a.clave)}variant="success">Modificar</Button></td>
                </tr>
              )
            }
              </tbody>
            </Table>
          }

          </div>
     );
}
 
export default Listado;