import './App.css';
import React, { Component } from 'react';
import Swal from 'sweetalert2';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './Components/Header';
import Formulario from './Components/Formulario';
import Listado from './Components/Listado';
import firebase from './Settings/ConfigFirebase.js'


class App extends Component {
  constructor() {
    super();
    this.state = {
      persona:{
        clave:"",
        nombre:"",
        correo:"",
        municipio:""
      },
      lista:[],
      desactivado:false,
    };
  }
  
  componentDidMount(){
    firebase.database().ref('Alumnos').on('value', snapshot=> {
      let personasLista=[];
      snapshot.forEach(row=>{
        personasLista.push({
          clave:row.key,
            nombre:row.val().nombre,
            correo:row.val().correo,
            municipio:row.val().municipio
          })
      })
      this.setState({
        ...this.state,
        lista:personasLista
      })
    });
  }


  guardarCambios=(e)=>{
    this.setState({ 
      ...this.state,
      persona:{
      ...this.state.persona,  
      [e.target.name]: e.target.value
      } 
      //nombre:e.target.value 
    });
  }
  
  eliminar=(id)=>{

    firebase.database().ref('Alumnos/' + id).set(null).then(() => {
      Swal.fire({
        position: 'center',
        icon: 'error',
        title: 'Eliminado',
        showConfirmButton: false,
        timer: 1500
      })
    });

    const temporal = this.state.lista.filter(a=>a.clave!==id)
    this.setState({
      ...this.state,
      lista:temporal
    })
  }

  modificar=(id)=>{
    const temporal = this.state.lista.find(a=>a.clave===id);
    this.setState({
      ...this.state,
      persona:{
        clave:temporal.clave,
        nombre:temporal.nombre,
        correo:temporal.correo,
        municipio:temporal.municipio
      },
      desactivado:true
    })
  }
  enviar=(e)=>{
    e.preventDefault();

    const {clave,nombre, correo, municipio} = this.state.persona;
      
    const vacios = (clave.length===0 && nombre.length===0 && correo.length===0 && municipio.length===0) || municipio==="selecciona" 

    if(!vacios){

      firebase.database().ref('Alumnos/' + clave).update(this.state.persona).then(() => {
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Registrado',
          showConfirmButton: false,
          timer: 1500
        })
      });


      
      
      let temporal = this.state.lista;

      if(this.state.desactivado===true){
        temporal= temporal.filter(a=>a.clave!==clave)
      }

      this.setState({
        ...this.state,
        lista:[...temporal,this.state.persona],
        persona:{
          clave:"",
          nombre:"",
          correo:"",
          municipio:""
        },
        desactivado:false
      })
    }
    else{
      Swal.fire({
        position: 'center',
        icon: 'error',
        title: 'Llena todos los campos',
        showConfirmButton: false,
        timer: 1500
      })
      return;
    }
    
  }
  
  render() {
    
    return (
      <div className="App">
        <Header/>
        <div className="Containers">
          <Formulario
              enviar={this.enviar}
              guardarCambios={this.guardarCambios}
              persona={this.state.persona}
              desactivado={this.state.desactivado}
          />
          <Listado
            lista={this.state.lista}
            eliminar={this.eliminar}
            modificar={this.modificar}
          />
          
        </div>
      </div>
    )
  }
}

export default App;
